import React from 'react'
import ReactPlayer from 'react-player'
import { observer } from 'mobx-react'

import store, { Cut, Mode } from '../../store'
import styles from './MultiPlayer.module.sass'

interface Props {
	updatePosition: (playedSeconds: number) => void
}

function MultiPlayer(props: Props) {
	const { updatePosition } = props
	// const playerRef = React.createRef<ReactPlayer>()

	// useEffect(() => {
	// 	if (store.mode === Mode.Show) store.changeModeToCreate()
	// })

	function onReady(index: number, player: ReactPlayer) {
		console.log(`Player ${index} onReady`)

		store.playersChain[index].instance = player
	}

	function onProgress(playerIndex: number, playerState: any) {
		const playerStore = store.playersChain[playerIndex]
		const cutIndex = store.playersChain[playerIndex].cutIndex
		if (playerStore.isVisible) updatePosition(playerState.playedSeconds)

		if (!cutIndex) {
			console.log('No cut index in player')
			return
		}
		const cut = store.cutsStore[cutIndex].cut
		console.log(
			`Player ${playerIndex} onProgress in ${playerState.playedSeconds} sec (s: ${cut?.startTime} e: ${cut?.endTime}`
		)

		switch (store.mode) {
			case Mode.Create:
				break
			case Mode.PlayOne:
				if (!playerStore.isVisible || isInCut(cut, playerState.playedSeconds)) return
				stopPlayOne(playerIndex)
				// store.changeModeToCreate()
				break
			case Mode.PlayAll:
				if (!playerStore.isVisible || isInCut(cut, playerState.playedSeconds)) return
				goToNextCut(playerIndex)
				break
			case Mode.Show:
				break
		}
	}

	function onBuffer(playerIndex: number) {
		console.log(`Player ${playerIndex} onBuffer`)
	}

	function onBufferEnd(playerIndex: number) {
		console.log(`Player ${playerIndex} onBufferEnd`)
	}

	function onStart(playerIndex: number) {
		console.log(`Player ${playerIndex} onStart`)
		// store.playersChain[playerIndex].playing = true
	}

	function onPlay(playerIndex: number) {
		console.log(`Player ${playerIndex} onPlay`)
		// store.playersChain[playerIndex].playing = true

		switch (store.mode) {
			case Mode.Create:
				break
			case Mode.PlayOne:
				break
			case Mode.PlayAll:
				preloadNextCut()
				break
			case Mode.Show:
				break
		}
	}

	function onPause(playerIndex: number) {
		console.log(`Player ${playerIndex} onPause`)
		// store.playersChain[playerIndex].playing = false
	}

	function onEnded(playerIndex: number) {
		console.log(`Player ${playerIndex} onEnded`)
		// store.playersChain[playerIndex].playing = false
	}

	function onSeek(playerIndex: number, e: any) {
		console.log(`Player ${playerIndex} onSeek`)
	}

	function preloadNextCut() {
		const nextPlayerId = store.nextPlayerIndex
		const nextCutIndex = store.nextCutIndex
		const startTime = store.cutsStore[nextCutIndex].cut?.startTime

		console.log(`Player ${nextPlayerId} START preload`)

		store.playersChain[nextPlayerId] = {
			...store.playersChain[nextPlayerId],
			cutIndex: store.cutsStore[nextCutIndex].index,
			url: store.cutsStore[nextCutIndex].url,
			muted: true,
			playing: true,
		}

		setTimeout(() => {
			store.playersChain[nextPlayerId].playing = false
			store.playersChain[nextPlayerId].muted = false
			store.playersChain[nextPlayerId].instance?.seekTo(startTime || 0)
			console.log(`Player ${nextPlayerId} END preload`)
		}, 1000)
	}

	function isInCut(cut: Cut | null | undefined, playedSeconds: number) {
		if (!cut) return false
		if (playedSeconds >= cut.startTime && playedSeconds <= cut.endTime) return true
		return false
	}

	function goToNextCut(playerIndex: number) {
		stopPlayOne(playerIndex)

		const isLastCut = store.currentCutIndex === store.cutsStore.length - 1

		if (isLastCut) {
			console.log('Last cut is end')
			store.changeModeToCreate()
		} else {
			console.log(`Go from ${store.currentCutIndex} to ${store.nextCutIndex} cut`)
			store.playersChain[store.activePlayerIndex].isVisible = false
			store.playersChain[store.nextPlayerIndex].isVisible = true
			store.playersChain[store.nextPlayerIndex].playing = true

			const nextPlayerIndex = store.nextPlayerIndex
			const nextCutIndex = store.nextCutIndex
			store.activePlayerIndex = nextPlayerIndex
			store.currentCutIndex = nextCutIndex
		}
	}

	function stopPlayOne(playerIndex: number) {
		// const cut = store.cutsStore[playerIndex].cut
		// const startTime = cut ? cut.startTime : 0

		store.playersChain[playerIndex].playing = false

		// @ts-ignore
		store.playersChain[playerIndex].instance?.getInternalPlayer().stopVideo()
		// store.playersChain[playerIndex].instance?.seekTo(startTime)
	}

	return (
		<div>
			{store.url ? (
				store.playersChain.map((item, index, array) => {
					const curIndex = array.length - 1 - index

					return (
						<div
							key={store.playersChain[curIndex].id}
							style={{ visibility: store.playersChain[curIndex].isVisible ? 'visible' : 'hidden' }}
						>
							<ReactPlayer
								// ref={playerRef}
								playing={store.playersChain[curIndex].playing}
								className={styles.player}
								url={store.playersChain[curIndex].url}
								muted={store.playersChain[curIndex].muted}
								width='100%'
								height='100%'
								onPlay={onPlay.bind(null, curIndex)}
								onProgress={onProgress.bind(null, curIndex)}
								onStart={onStart.bind(null, curIndex)}
								onPause={onPause.bind(null, curIndex)}
								onEnded={onEnded.bind(null, curIndex)}
								onReady={onReady.bind(null, curIndex)}
								onSeek={onSeek.bind(null, curIndex)}
								onBufferEnd={onBufferEnd.bind(null, curIndex)}
								onBuffer={onBuffer.bind(null, curIndex)}
								progressInterval={100}
								controls={true}
								config={{
									youtube: {
										playerVars: {},
									},
									// twitch: {
									// 	options: {
									// 		autoplay: true,
									// 		time: '0h0m0s',
									// 	},
									// },
								}}
							/>
						</div>
					)
				})
			) : (
				<div className={styles.playerPlaceholder} />
			)}
		</div>
	)
}

export default observer(MultiPlayer)
