import { observable, makeObservable, configure, computed, action } from 'mobx'
import { v4 as uuid } from 'uuid'

import parse from 'url-parse'
import ReactPlayer from 'react-player'

configure({ enforceActions: 'never' })

export interface Cut {
	startTime: number
	endTime: number
}

export enum IframeStatusType {
	Active = 'active',
	Inactive = 'inactive',
}

export interface Player {
	id: number
	isVisible: boolean
	isPreloading?: boolean
	playing: boolean
	muted: boolean
	url: string
	instance?: ReactPlayer
	cutIndex?: number
}

export interface CutStore {
	id: string
	index: number
	url: string
	cut?: Cut
}

export enum Mode {
	Create = 'create',
	PlayOne = 'playOne',
	PlayAll = 'playAll',
	Show = 'show',
}

const url = parse(window.location.href, true)
let urlStory: CutStore[] | null = null

if (url.query.story) {
	const raw = decodeURIComponent(url.query.story)
	urlStory = raw.split(',').map((player) => {
		const parts = player.split('::')

		return {
			id: parts[0],
			index: parseFloat(parts[1]),
			url: parts[2],
			cut: {
				startTime: parseFloat(parts[3]),
				endTime: parseFloat(parts[4]),
			},
		}
	})
}

const queryUrl = url.query.video ? decodeURIComponent(url.query.video) : null

class Store {
	@observable editMode = !queryUrl
	@observable url: string | null = queryUrl
	@observable mode: Mode = !queryUrl ? Mode.Create : Mode.Show
	@observable activePlayerIndex: number = 0
	@observable currentCutIndex: number = 0
	@observable cutsStore: CutStore[] = urlStory || [
		{
			id: uuid(),
			index: 0,
			url: this.url as string,
		},
	]
	@observable playersChain: Player[] = [0, 1, 2].map((player, i) => {
		return {
			id: i,
			isVisible: i === 0,
			isPreloading: false,
			playing: false,
			muted: false,
			url: this.cutsStore[0].url,
		}
	})

	@computed get shareUrl() {
		if (!this.url) return null
		const baseUrl = window.location.href.split('?')[0]
		return `${baseUrl}?video=${encodeURIComponent(this.url)}&story=${encodeURIComponent(
			this.cutsStore
				.map((player, i) => {
					return [player.id, player.index, player.url, player.cut?.startTime, player.cut?.endTime].join('::')
				})
				.join(',')
		)}`
	}

	@computed get trailerLength() {
		return this.cutsStore.reduce((duration, player, index) => {
			if (index === 0 || !player.cut) return duration
			return duration + player.cut.endTime - player.cut.startTime
		}, 0)
	}

	@computed get nextPlayerIndex() {
		if (this.activePlayerIndex === this.playersChain.length - 1) return 0
		return this.activePlayerIndex + 1
	}

	@computed get nextCutIndex() {
		if (this.currentCutIndex === this.cutsStore.length - 1) return 1
		return this.currentCutIndex + 1
	}

	@action
	addCut(cut: Cut) {
		this.cutsStore.push({
			id: uuid(),
			index: this.cutsStore.length,
			url: this.url as string,
			cut: {
				startTime: cut.startTime,
				endTime: cut.endTime,
			},
		})
	}

	@action
	removeCut(cutIndex: number) {
		if (
			this.playersChain[this.activePlayerIndex].playing &&
			(cutIndex === this.currentCutIndex || cutIndex === this.nextCutIndex)
		)
			return

		if (cutIndex === this.currentCutIndex) {
			this.changeModeToCreate()
			return
		}

		this.cutsStore = this.cutsStore
			.filter((item) => item.index !== cutIndex)
			.map((item, index) => {
				item.index = index
				return item
			})
	}

	@action
	changeUrl(url: string) {
		this.url = url
		this.cutsStore = []
		this.cutsStore.push({
			id: uuid(),
			index: 0,
			url,
		})

		this.changeModeToCreate()
	}

	@action
	changeModeToCreate() {
		this.mode = Mode.Create
		console.log('--> In CREATE mode')

		for (let i = 0; i < this.playersChain.length; i += 1) {
			if (i === this.activePlayerIndex) {
				this.playersChain[i] = {
					...this.playersChain[i],
					isVisible: true,
					url: this.cutsStore[0].url,
					cutIndex: 0,
					playing: false,
				}

				this.currentCutIndex = 0
			} else {
				this.playersChain[i].isVisible = false
				this.playersChain[i].playing = false
			}
			// this.playersChain[i].instance?.seekTo(0)
			// @ts-ignore
			this.playersChain[i].instance?.getInternalPlayer().stopVideo()
		}
	}

	@action
	changeModeToPlayOne(cutIndex: number) {
		this.mode = Mode.PlayOne
		console.log('--> In PLAYONE mode')

		this.playersChain[this.activePlayerIndex] = {
			...this.playersChain[this.activePlayerIndex],
			url: this.cutsStore[cutIndex].url,
			cutIndex,
			playing: false,
		}
		this.currentCutIndex = cutIndex

		this.startPlayingCurrentCut()
	}

	@action
	changeModeToPlayAll() {
		if (this.cutsStore.length === 1) {
			this.changeModeToCreate()
			this.playersChain[this.activePlayerIndex].playing = true
			return
		}

		this.mode = Mode.PlayAll
		console.log('--> In PLAYALL mode')

		this.playersChain[store.activePlayerIndex].playing = false

		setTimeout(() => {
			for (let i = 0; i < this.playersChain.length; i += 1) {
				// @ts-ignore
				this.playersChain[i].instance?.getInternalPlayer().stopVideo()

				if (i === 0) {
					this.playersChain[i] = {
						id: this.playersChain[i].id,
						instance: this.playersChain[i].instance,
						isVisible: true,
						muted: false,
						url: this.cutsStore[1].url,
						playing: false,
						cutIndex: 1,
					}
				} else {
					this.playersChain[i] = {
						id: this.playersChain[i].id,
						instance: this.playersChain[i].instance,
						isVisible: false,
						muted: false,
						url: this.cutsStore[1].url,
						playing: false,
					}
				}
			}

			this.activePlayerIndex = 0
			this.currentCutIndex = 1
			this.startPlayingCurrentCut()
		}, 1000)
	}

	@action
	startPlayingCurrentCut() {
		const cut = this.cutsStore[this.currentCutIndex].cut

		if (!cut) return
		this.playersChain[this.activePlayerIndex].instance?.seekTo(cut.startTime)
		setTimeout(() => {
			this.playersChain[this.activePlayerIndex].instance?.seekTo(cut.startTime)
			this.playersChain[this.activePlayerIndex].playing = true
		}, 1000)
	}

	constructor() {
		makeObservable(this)
	}
}

const store = new Store()

export default store
